body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
    height: 6%;
    width: 6%;
    border: 1px solid #78C2AD;
}

.navbar-light .navbar-nav .color-link {
    color: #78C2AD;
}

.navbar-expand .navbar-nav .color-link {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}

.nav-tabs .color-link {
    border: 1px solid transparent;
    border-top-left-radius: 0.4rem;
    border-top-right-radius: 0.4rem;
}

.nav-tabs .color-link:hover, .no-link-underline:hover {
    text-decoration: none;
    color: #4aa48a;
    border-color: #f7f7f9 #f7f7f9 #eceeef;
}

.color-link {
    display: block;
    padding: 0.5rem 1rem;
}

.characteristics {
    text-align: center;
    width: 50%;
    min-width: 470px;
    margin: 20px auto;
}

.characteristics .btn {
    width: 150px;
}

.goal-selector {
    text-align: center;
    width: 50%;
    min-width: 470px;
    margin: 20px auto;
}

.goal-selector .btn {
    width: 150px;
}

.nutrition-ratio-selector {
    text-align: center;
    width: 50%;
    min-width: 470px;
    margin: 20px auto;
}

.nutrition-ratio-selector .btn {
    width: 150px;
}

.nutrition-display {
    margin: 20px;
}

.btn-container {
    margin: 1%;
    float: right;
}

.table {
    text-align: center;
    overflow-x: auto;
    clear: both;
}

.product-row {
    align-items: center;
    width: 100%;
    min-width: 1200px;
    display: flex;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    padding: 5px 15px;
}

.product-row:hover {
    background-color: #ececec;
}

.headers {
    border-top: 1px solid rgba(0, 0, 0, 0.05);
    background-color: #f8f9fa;
    padding: 8px 15px;
}

.headers:hover {
    background-color: #f8f9fa;
}

.cell {
    flex-grow: 1;

    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    padding: 0.25%;
    width: 9%;
    min-width: 100px;
}

.header {
    font-size: 16px;
    font-weight: 700;
}

.delete-btn {
    width: 130px;
}

.delete-row {
    min-width: 140px;
}

.add-btn {
    width: 230px;
}

.add-row-btn {
    min-width: 230px;
    align-self: flex-end;
    margin: 16px;
}

.product-select-container {
    display: flex;
    justify-content: space-between;
}

.cell-input {
    text-align: center;
    width: 100%;
    border: none;
    color: #888;
}

@media (max-width: 760px) {
    .product-select-container {
        display: grid;
    }

    .add-row-btn {
        margin-top: 0;
    }
}

.product-select {
    display: inline-block;
}

.product-select-form {
    text-align: center;
    width: 50%;
    min-width: 470px;
    margin: 15px;
}

.product-select-form .btn {
    width: 230px;
}

.product-select-form .btn-secondary {
    margin-right: 5px;
}

.product-select-form .btn-primary {
    margin-left: 5px;
}

.about {
    margin: 2%;
}

.paragraph {
    margin-bottom: 2%;
}

.const-table {
    width: 800px;
    margin: 0.5% 0;
}

.const-table th, .const-table td {
    border-bottom: 2px solid #ececec;
    padding: 10px;
}

.tips-list {
    width: 80%;
    text-align: justify;
}

.tips-list li {
    margin: 0.5%;
}

.not-found-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.not-found-content {
    flex: 1 1;
    text-align: center;
    vertical-align: center;
    background-image: linear-gradient(to bottom, #f8f9fa, #4aa48a);
    /*    background-image: linear-gradient(to right, #4aa48a, palevioletred); #f8f9fa , #4aa48a, #f8f9fa, #F3969A*/
}

.main-text {
    color: white;
    font-size: 23vw;
    font-weight: 300;
}

.text {
    color: white;
    font-size: 20px;
}

.home-link {
    color: white;
    font-size: 25px;
}

.home-link:hover {
    color: #f8f9fa;
}
